
.ska-box::-webkit-scrollbar,
body::-webkit-scrollbar {
    max-width: 1vw;
}

.ska-box::-webkit-scrollbar-track,
body::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(48, 49, 48, 0.3);
    border-radius: 2px;
}

.ska-box::-webkit-scrollbar-thumb,
body::-webkit-scrollbar-thumb {
    border-radius: 2px;
    box-shadow: inset 0 0 6px rgba(150, 20, 150, 0.8);
}

a {
  text-decoration: none !important;
}

a:hover {
  transform: scale(1.05);
  transition: 0.3s;
}

.ttable {
  height: auto;
  overflow-y: hidden;
  width: 100%;
  overflow-x: auto;
  padding-top: 30px;
}

.content-table {
  border: 1px solid #000;
  width: 100%;
  margin: 0 0 20px;
}

.content-table thead {
  color: #fff;
  background-color: var(--bs-cfs-primary-light);
}

#admin-content .content-table th {
  padding: 10px;
  border: 1px solid var(--bs-cfs-primary);
  font-weight: 600;
  text-align: center;
  text-transform: uppercase;
}

.content-table tbody {
  color: #333;
}

.content-table tbody tr {
  background-color: rgb(255, 246, 255);
}

.content-table tbody tr:nth-child(even) {
  background-color: rgb(248, 211, 248);
}

.content-table tbody td {
  padding: 10px;
  border: 1px solid var(--bs-cfs-primary-light);
  text-align: center;

}

.content-table tbody td:nth-child(2) {
  text-align: left;
}


.main {
  /* position: relative; */
  width: 360px;
  min-height: 480px;
  margin: 50vh auto;
  background: rgba(var(--bs-cfs-primary-rgb), .7);
  box-shadow: 5px 5px 20px 1px #666;
  padding: 15px;
  border-radius: 15px;
  overflow: hidden;
}

.authlogo {
  height: 60px
}

@media only screen and (max-width: 768px) {
  .main {
    width: 300px;
  }

  .authlogo {
    height: 45px
  }
}


.cfs-nav {
  font-weight: bold;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.205);
}

.cfs-bg {
  background-color: rgb(255, 246, 255);
  min-height: 100vh;
}

.cfs-box {
  border-radius: 5px;
  background: linear-gradient(125deg, #fff, rgb(255, 248, 255));
  box-shadow: 0 0 15px rgba(98, 99, 98, 0.205);
  margin: 10px;
  padding: 10px 15px;
}

.cfs-hr {
  /* width: 98%; */
  height: 2px;
  margin: 5px auto;
  background-color: var(--bs-cfs-primary);
  border-radius: 2px;
  opacity: 1;
}
.cfs-small-text{
  font-size: 0.7rem;
  font-weight: 500;
  color: var(--bs-cfs-primary);
  overflow: hidden;
}
.offcanvas {
  background-color: var(--bs-cfs-primary-dark);

}

/* pagination */

.pagination {
  margin: 5px auto;
  display: flex;
  list-style: none;
  outline: none;
}

.pagination>.active>a {
  background-color: var(--bs-cfs-primary);
  border-color: var(--bs-cfs-primary);
  color: #fff;
}

.pagination>li>a {
  border: 1px solid #e5dee6;
  padding: 7px 10px;
  outline: none;
  cursor: pointer;
}

.pagination>.active>a,
.pagination>.active>span,
.pagination>.active>a:hover,
.pagination>.active>span:hover,
.pagination>.active>a:focus,
.pagination>.active>span:focus {
  background-color: var(--bs-cfs-primary);
  border-color: var(--bs-cfs-primary);
  outline: none;
}

.pagination>li>a,
.pagination>li>span {
  color: var(--bs-cfs-primary)
}

.pagination>li:first-child>a,
.pagination>li:first-child>span {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}

.pagination>li:last-child>a,
.pagination>li:last-child>span {
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

























.form-control:focus,
.form-check-input:focus,
.form-select:focus {
  border-color: #ea86fe;
  -webkit-box-shadow: 0 0 0 0.25rem rgba(var(--bs-cfs-primary-rgb), .7);
  box-shadow: 0 0 0 0.25rem rgba(var(--bs-cfs-primary-rgb), .5);
}

.form-check-input:checked {
  background-color: var(--bs-cfs-primary);
  border-color: var(--bs-cfs-primary);
}

.page-link:hover {
  color: var(--bs-cfs-primary);
}

.page-link:focus {
  color: var(--bs-cfs-primary);
  -webkit-box-shadow: 0 0 0 0.25rem var(--bs-cfs-primary-light);
  box-shadow: 0 0 0 0.25rem var(--bs-cfs-primary-light);
}

.page-item.active .page-link {
  background-color: var(--bs-cfs-primary);
  border-color: var(--bs-cfs-primary);
}

.page-item.disabled .page-link {
  color: #7d6c7d;
  border-color: #e4dee6;
}


.table-cfs {
  --bs-table-bg: rgb(250, 233, 250);
  --bs-table-striped-bg: rgb(252, 222, 252);
  --bs-table-striped-color: #000;
  --bs-table-active-bg: #dfe0e1;
  --bs-table-active-color: #000;
  --bs-table-hover-bg: #e5e6e7;
  --bs-table-hover-color: #000;
  color: #000;
  border-color: #dfe0e1;
}

.dropdown-menu[data-bs-popper] {
  top: 100%;
  left: -60px;
  margin-top: 0.125rem;
}

.text-cfs-primary {
  --bs-text-opacity: 1;
  color: rgba(var(--bs-cfs-primary-rgb), var(--bs-text-opacity)) !important;
}

.text-cfs-secondary {
  --bs-text-opacity: 1;
  color: rgba(var(--bs-cfs-secondary-rgb), var(--bs-text-opacity)) !important;
}

.bg-cfs-primary {
  --bs-bg-opacity: 1;
  background-color: rgba(var(--bs-cfs-primary-rgb), var(--bs-bg-opacity)) !important;
}


.bg-cfs-primary-light {
  --bs-bg-opacity: 1;
  background-color: rgba(var(--bs-cfs-primary-light-rgb), var(--bs-bg-opacity)) !important;
}


.bg-cfs-primary-dark {
  --bs-bg-opacity: 1;
  background-color: rgba(var(--bs-cfs-primary-dark-rgb), var(--bs-bg-opacity)) !important;
}


.bg-cfs-secondary {
  --bs-bg-opacity: 1;
  background-color: rgba(var(--bs-cfs-secondary-rgb), var(--bs-bg-opacity)) !important;
}